import React from 'react'
import styled from 'styled-components'
import { NavLink } from 'react-router-dom'
import {useWallet} from "use-wallet";
import useModal from "../../hooks/useModal";
import AccountModal from "../AccountModal";

const Nav: React.FC = () => {
    return (
        <StyledNav>
            <StyledLink exact activeClassName="active" to="/mamms">M-AMMs</StyledLink>
            <StyledLink exact activeClassName="active" to="/mmfs">MMFs</StyledLink>
            <StyledLink exact activeClassName="active" to="/reorders">ReOrders</StyledLink>
            <StyledLink exact activeClassName="active" to="/dashboard">Dashboard</StyledLink>
            <AccountButton />
        </StyledNav>
    )
}

interface AccountButtonProps {}

const AccountButton: React.FC<AccountButtonProps> = (props) => {
    const [onPresentAccountModal] = useModal(<AccountModal />)

    const { account, connect } = useWallet()
    return (
        <StyledAccountButton>
            {!account ? (
                <StyledWallet onClick={() => connect('injected')}>
                    Unlock Wallet
                </StyledWallet>

            ) : (
                <StyledWallet onClick={onPresentAccountModal}>
                    My Wallet
                </StyledWallet>
            )}
        </StyledAccountButton>
    )
}

const StyledAccountButton = styled.div`
`

const StyledNav = styled.nav`
    align-items: center;
    display: flex;
`

const StyledLink = styled(NavLink)`
    color: ${props => props.theme.color.grey[300]};
    font-weight: 500;
    font-size: 14px;
    margin-left: ${props => props.theme.spacing[1]}px;
    margin-right: ${props => props.theme.spacing[1]}px;
    padding-top: ${props => props.theme.spacing[1]}px;
    padding-bottom: ${props => props.theme.spacing[1]}px;
    padding-left: ${props => props.theme.spacing[2]}px;
    padding-right: ${props => props.theme.spacing[2]}px;
    text-decoration: none;
    transition: all 0.3s;
    border-bottom: 2px solid transparent;
    cursor: pointer;
    &:hover {
        color: ${props => props.theme.color.grey[300]};
        border-bottom: 2px solid ${props => props.theme.color.grey[300]};
    }
    &.active {
        color: ${props => props.theme.color.grey[300]};
        border-bottom: 2px solid ${props => props.theme.color.grey[300]};
    }
`

const StyledWallet = styled.button`
    color: ${props => props.theme.color.grey[100]};
    font-weight: 500;
    font-size: 14px;
    margin-left: ${props => props.theme.spacing[3]}px;
    padding-top: ${props => props.theme.spacing[1]}px;
    padding-bottom: ${props => props.theme.spacing[1]}px;
    padding-left: ${props => props.theme.spacing[3]}px;
    padding-right: ${props => props.theme.spacing[3]}px;
    text-decoration: none;
    outline:none;
    transition: all 0.3s;
    border: 0px solid #F0B90B;
    background-color: #000000;
    border-radius: 8px;
    cursor: pointer;
    &:hover {
        background-color: #08F3CB;
        color: ${props => props.theme.color.grey[900]};
    }
`

export default Nav
