import React, {useCallback, useEffect, useState} from 'react';
import styled from 'styled-components';
import Modal, { ModalProps } from '../Modal';
import TokenSymbol from "../TokenSymbol";
import useTiTICoreContracts from "../../hooks/useTiTICoreContracts";

const AccountModal: React.FC<ModalProps> = ({ onDismiss }) => {
    //const basisCash = useBasisCash();

    //const bacBalance = 0;

    //const basBalance = 0;
    const displayBibeBalance = 0.000;
    const [displayBeiBalance, setDisplayBeiBalance] = useState<number>(0);
    const beiCash = useTiTICoreContracts();

    const fetchStats = useCallback(async () => {
        const [titiBalance] = await Promise.all([
            beiCash.getTITIBalance(),
        ]);

        setDisplayBeiBalance(titiBalance);
    }, [beiCash]);

    useEffect(() => {
        if (beiCash) {
            fetchStats().catch((err) => console.error(err.stack));
        }
        // eslint-disable-next-line react-hooks/exhaustive-deps
    }, [beiCash?.isUnlocked, fetchStats]);


    return (
        <Modal>
            <div style={{fontSize: "28px", padding: "20px 5px 30px", textAlign: "center"}}>My Wallet</div>
            <Balances>
                <StyledBalanceWrapper>
                    <TokenSymbol symbol="TiTi" />
                    <StyledBalance>
                        <StyledValue>{displayBeiBalance}</StyledValue>
                        <div style={{fontSize: "20px", padding: "5px 10px 20px", color: "#5D5A4D"}}>TiTi Balance</div>
                    </StyledBalance>
                </StyledBalanceWrapper>
                <StyledBalanceWrapper>
                    <TokenSymbol symbol="TTG" />
                    <StyledBalance>
                        <StyledValue>{displayBibeBalance.toFixed(3)}</StyledValue>
                        <div style={{fontSize: "20px", padding: "5px 10px 20px", color: "#5D5A4D"}}>TTG Balance</div>
                    </StyledBalance>
                </StyledBalanceWrapper>
            </Balances>
        </Modal>
    )
}

const StyledValue = styled.div`
  color: #5D5A4D;
  font-size: 30px;
  font-weight: 500;
  padding: 20px 10px 20px;
`

const StyledBalance = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
`

const Balances = styled.div`
  display: flex;
  flex-direction: row;
  justify-content: center;
  margin-bottom: ${props => props.theme.spacing[4]}px;
`

const StyledBalanceWrapper = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 0 ${props => props.theme.spacing[3]}px;
`

export default AccountModal
