import React from 'react';

import titiLogo from "../../assets/img/icon/logo.svg";
import ttgLogo from "../../assets/img/icon/ttg.svg";

import beiLogo from "../../assets/img/icon/b_9.png";
import bibeLogo from "../../assets/img/icon/b_10.png";
import bnbLogo from "../../assets/img/icon/bnbLogo.png";
import btcbLogo from "../../assets/img/icon/btcbLogo.png";
import ethbLogo from "../../assets/img/icon/ethbLogo.png";
import busdLogo from "../../assets/img/icon/busdLogo.png";
import pancakeLogo from "../../assets/img/icon/pancakeLogo.png";
import autoFundLogo from "../../assets/img/icon/autoFundLogo.png";
import bakeLogo from "../../assets/img/icon/bakeLogo.png";
import xvsLogo from "../../assets/img/icon/xvsLogo.png";
import alpacaLogo from "../../assets/img/icon/alpacaLogo.png";
import lpLogo from "../../assets/img/icon/lpLogo.png"

const logosBySymbol: {[title: string]: string} = {
    'TiTi': titiLogo,
    'TTG': ttgLogo,
    'BEI': beiLogo,
    'BIBE': bibeLogo,
    'BNB': bnbLogo,
    "BTCB": btcbLogo,
    "ETH": ethbLogo,
    "BUSD": busdLogo,
    "Pancake": pancakeLogo,
    "AutoFund": autoFundLogo,
    "Bake": bakeLogo,
    "XVS": xvsLogo,
    "Alpaca": alpacaLogo,
    "LPLogo": lpLogo
};

type logoProps = {
    symbol: string;
    size?: number;
}

const TokenSymbol: React.FC<logoProps> = ({ symbol, size = 64 }) => {
    if (!logosBySymbol[symbol]) {
        throw new Error(`Invalid BasisLogo symbol: ${symbol}`);
    }
    return (
        <img
            src={logosBySymbol[symbol]}
            alt={`${symbol} Logo`}
            width={size}
            height={size}
        />
    )
};

export default TokenSymbol;
