import React from "react";
import styled from 'styled-components'
import Logo from './Logo'
import Nav from "./Nav";

const AppTopMenu: React.FC = () => {
    return (
        <StyleTopMenu>
            <StyledContainer>
                <StyledTopMenuInner>
                    <div style={{flex: 1}}>
                        <Logo/>
                    </div>
                    <Nav/>
                </StyledTopMenuInner>
            </StyledContainer>
        </StyleTopMenu>
    )
}

const StyleTopMenu = styled.div`
    position: position;
    width: 100vw;
    background-color: ${props => props.theme.topBarBG};
    transition: all 0.3s;
`

const StyledContainer = styled.div`
    box-sizing: border-box;
    margin: 0 auto;
    max-width: 1000px;
    padding: 0 10px;
    width: 100%;
`

const StyledTopMenuInner = styled.div`
    align-items: center;
    display: flex;
    height: 72px;
    justify-content: space-between;
    max-width: ${props => props.theme.siteWidth}px;
    width: 100%;
    flex-wrap: wrap;
    transition: all 0.3s;
    
    @media (max-width: 768px) {
        display: flex;
        justify-content: center;
        width: 100%;
        flex-flow: column nowrap;
        align-items: center;
        height: 80px;
        padding-top: 20px;
        padding-bottom: 20px;
    }
`

export default AppTopMenu;
